import Vue from "vue";
import Vuex from "vuex";
import router from "../router";
import { getDayWeather, getWeekWeather } from "@/api/api.js";

Vue.use(Vuex);

export default new Vuex.Store({
  strict: process.env.NODE_ENV !== "production",
  state: {
    weathers: [],
    weathersWeek: [],
    time: 0,
    sort: "",
    location: "",
    selected: "",
    city: "",
    loading: false,
  },
  mutations: {
    LOADING(state, status) {
      state.loading = status;
    },
    WEATHERS(state, payload) {
      state.weathers = payload;
    },
    WEATHERSWEEK(state, payload) {
      state.weathersWeek = payload;
    },
    TIME(state, payload) {
      state.time = payload;
    },
    SORT(state, payload) {
      state.sort = payload;
    },
    LOCATION(state, payload) {
      state.location = payload;
    },
    SELECTED(state, payload) {
      state.selected = payload;
    },
    CITY(state, payload) {
      state.city = payload;
    },
  },
  actions: {
    // 
    async getWeathers(context) {
      await getDayWeather()
        .then((res) => {
          context.commit("WEATHERS", res.records.location);
        })
        .catch((err) => console.log(err));
    },
    // 
    async getWeathersWeek(context, payload) {
      let tmp = {};
      context.dispatch("transform", payload);
      await getWeekWeather()
        .then((res) => {
          tmp = res.records.locations[0].location.filter((item) => {
            if (item.locationName === context.state.location) {
              return item;
            }
          });
          context.commit("WEATHERSWEEK", tmp);
        })
        .catch((err) => console.log(err));
    },
    // 
    changeSort(context, payload) {
      context.dispatch("transformE", payload);
      let sort = context.state.sort;
      if (router.history.current.name === sort) return;
      if (sort == "" && router.history.current.name == "Main") return;
      router.push(`./${sort}`);
      context.dispatch("getWeathersWeek", sort);
    },
    transformE(context, payload) {
      if (payload == "") context.commit("SORT", "");
      if (payload == "") context.commit("SORT", "keelung");
      if (payload == "") context.commit("SORT", "taipei");
      if (payload == "") context.commit("SORT", "newtaipei");
      if (payload == "") context.commit("SORT", "taoyuan");
      if (payload == "") context.commit("SORT", "hsinchucity");
      if (payload == "") context.commit("SORT", "hsinchu");
      if (payload == "") context.commit("SORT", "miaoli");
      if (payload == "") context.commit("SORT", "taichung");
      if (payload == "") context.commit("SORT", "changhua");
      if (payload == "") context.commit("SORT", "nantou");
      if (payload == "") context.commit("SORT", "yunlin");
      if (payload == "") context.commit("SORT", "chiayicity");
      if (payload == "") context.commit("SORT", "chiayi");
      if (payload == "") context.commit("SORT", "yilan");
      if (payload == "") context.commit("SORT", "hualien");
      if (payload == "") context.commit("SORT", "taitung");
      if (payload == "") context.commit("SORT", "tainan");
      if (payload == "") context.commit("SORT", "kaohsiung");
      if (payload == "") context.commit("SORT", "pingtung");
      if (payload == "") context.commit("SORT", "lienchiang");
      if (payload == "") context.commit("SORT", "kinmen");
      if (payload == "") context.commit("SORT", "penghu");
    },
    transform(context, payload) {
      if (payload == "") context.commit("LOCATION", "");
      if (payload == "keelung") context.commit("LOCATION", "");
      if (payload == "taipei") context.commit("LOCATION", "");
      if (payload == "newtaipei") context.commit("LOCATION", "");
      if (payload == "taoyuan") context.commit("LOCATION", "");
      if (payload == "hsinchucity") context.commit("LOCATION", "");
      if (payload == "hsinchu") context.commit("LOCATION", "");
      if (payload == "miaoli") context.commit("LOCATION", "");
      if (payload == "taichung") context.commit("LOCATION", "");
      if (payload == "changhua") context.commit("LOCATION", "");
      if (payload == "nantou") context.commit("LOCATION", "");
      if (payload == "yunlin") context.commit("LOCATION", "");
      if (payload == "chiayicity") context.commit("LOCATION", "");
      if (payload == "chiayi") context.commit("LOCATION", "");
      if (payload == "yilan") context.commit("LOCATION", "");
      if (payload == "hualien") context.commit("LOCATION", "");
      if (payload == "taitung") context.commit("LOCATION", "");
      if (payload == "tainan") context.commit("LOCATION", "");
      if (payload == "kaohsiung") context.commit("LOCATION", "");
      if (payload == "pingtung") context.commit("LOCATION", "");
      if (payload == "lienchiang") context.commit("LOCATION", "");
      if (payload == "kinmen") context.commit("LOCATION", "");
      if (payload == "penghu") context.commit("LOCATION", "");
    },
    // 
    active(context, payload) {
      context.commit("CITY", payload);
      let span = document.createElement("span");
      let node = document.createTextNode(payload);
      span.appendChild(node);
      let element = document.querySelector(".taiwan");
      element.appendChild(span);
      element.querySelectorAll("span").forEach((item) => {
        item.style.setProperty("position", "absolute");
        item.style.setProperty("top", `${event.pageY + 20}px`);
        item.style.setProperty("left", `${event.pageX + 20}px`);
        item.style.setProperty("background-color", "black");
        item.style.setProperty("color", "white");
        item.style.setProperty("padding", "0px 8px");
        item.style.setProperty("line-height", "30px");
        item.style.setProperty("font-size", "13px");
        item.style.setProperty("border-radius", "3px");
      });
    },
    // 
    inactive(context) {
      context.commit("CITY", "");
      let element = document.querySelector(".taiwan");
      let span = element.querySelectorAll("span");
      span.forEach((item) => {
        element.removeChild(item);
      });
    },
  },
  getters: {
    loading(state) {
      return state.loading;
    },
    sort(state) {
      let tmp = {};
      tmp.north = [];
      tmp.central = [];
      tmp.east = [];
      tmp.south = [];
      tmp.offshore = [];
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.north.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.north.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.north.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.north.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.north.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.north.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.north.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.central.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.central.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.central.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.central.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.central.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.central.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.east.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.east.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.east.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.south.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.south.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.south.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.offshore.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.offshore.push(element);
      });
      state.weathers.forEach((element) => {
        if (element.locationName === "") tmp.offshore.push(element);
      });
      return tmp;
    },
  },
  modules: {},
});
